#board {
    width: 400px;
    height: 400px;
    color: white;
}

#board div {
    background: #333;
    display: inline-block;
    width: 10%;
    height: 10%;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    border: 1px solid white;
}

#board .empty {
    background: #3a8c60;
}

#board .bomb {
    background: #9a2d2d;
}

#board .flag {
    background: #ea8591;
}
